		max-width: 100%;
		max-height: 100%;
		width: 27px;
		height: 27px;
		fill: rgba(#fff, .5);
	}
	&--center{
		justify-content: center;
	}
	&:hover{
		.swiper-grap-nav__cursor{
			border-color: rgba($color-primary, .5);
			svg{
				fill: rgba($color-primary, .5);
			}
		}
	}
}
.swiper-grap-nav__item{
	margin: 0 5px;
	display: flex;
}
.swiper-grap-nav__cursor{
	border: 1px solid rgba(#fff, .5);
	border-radius: 50%;
    width: 55px;
	height: 55px;
	display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-grap-nav__prev,
.swiper-grap-nav__next{
	cursor: pointer;
	&.swiper-button-disabled{
		cursor: default;
		opacity: .5;
	}
}

.swiper-grap--dark{
	svg{
		fill: rgba(#000, .35);
	}
	.swiper-grap-nav__cursor{
		border-color: rgba(#000, .35);
	}
}