.fab {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.fab__button {
  width: 50px;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s ease-in-out;
}

.fab__button > svg {
  width: 24px;
  height: 24px;
  fill: #fff;
  transition: all 0.2s ease-in-out;
}

.fab__button:hover > svg {
  fill: #fecd08;
}

.fab__trigger {
  width: 70px;
  height: 70px;
  background-color: #00864f;
  cursor: pointer;
}

.fab__actions {
  position: absolute;
  padding: 0;
  margin: 0;
  list-style: none;
  left: 70px;
  display: flex;
  flex-direction: row-reverse;
}

.fab__actions-item {
  opacity: 0;
  visibility: hidden;
  transform: translateX(-10px);
  transition: all 0.1s ease-in-out;
}

@supports (margin-inline-end: 20px) {
  .fab__actions-item {
    margin-inline-end: 20px;
  }
}

@supports not (margin-inline-end: 20px) {
  .fab__actions-item {
    margin-left: 20px;
  }
}

.fab__actions-item:nth-child(2) {
  transition-delay: .1s;
}

.fab__actions-item:nth-child(1) {
  transition-delay: .2s;
}

.fab.is-opened .fab__trigger {
  -moz-transform: rotate(270deg);
  -o-transform: rotate(270deg);
  -ms-transform: rotate(270deg);
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}

.fab.is-opened .fab__actions-item {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

