.l-tourism{
    background: url("#{$img-path}/tourism.jpg") no-repeat center center;
    background-size: cover;
    justify-content: space-between;
    .l-home-title{
        max-width: 550px;
        padding: 2em;
        margin: auto;
        position: relative;
        border-top: 1px solid #fff;
        &:after,
        &:before{
            content: '';
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            width: 1px;
            background-image: linear-gradient( 0deg, rgba(255,255,255,0) 0%, rgb(255,255,255) 100%); 
        }
        &:before{
            right: auto;
            left: 0;
        }
    }
}

.l-tourism__bottom{
    .row{
        align-items: flex-end;
    }
    .card__image{
        > img{
            max-height: 145px;
        }
    }
    .card--absolute  .card__content{
        padding: 2% 6%;
    }
}
