.l-home-section{
    height: 100vh;
    position: relative;
    padding: 6vh 0;
    min-height: 600px;
    display: flex;
    flex-direction: column;
}
.l-welcome-buttom{
   display: flex; 
   align-items: center;
}

.l-home-title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    &--center{
        justify-content: center;
        text-align: center;
    }
    &--col{
        flex-direction: column;
    }
}

#fp-nav{
    $fp-nav-color: #fff;
    position: fixed;
    left: 1vw;
    top: 50%;
    background: url('data:img/png;base64,') no-repeat bottom center;
    padding-bottom: 60px;
    > ul{
        margin-bottom: 0;
        list-style: none;
        padding: 0;
        &:after{
            content: '';
            width: 1px;
            height: 40px;
            background: rgba($fp-nav-color, .4);
            display: block;
            margin: 20px auto 0;
        }
    }
    li{
        margin: 5px 0;
    }
    a{
        border-radius: 50%;
        display: block;
        padding: 5px;
        margin: auto;
        border: 1px solid transparent;

        &.active{
            border-color: $fp-nav-color;
            span{
                background: $fp-nav-color;
            }
        }
    }
    span{
        width: 10px;
        height: 10px;
        display: block;
        border-radius: 50%;
        margin: auto;
        background: rgba($fp-nav-color, .4);
        @include transition();
        &:hover{
            background: $fp-nav-color;
        }
    }
    &.fp-nav--is-dark{
        $fp-nav-color: #000;
        background-image: url('data:img/png;base64,');
        > ul{
            &:after{
                background: rgba($fp-nav-color, .4);
            }
        }
        a{
            &.active{
                border-color: $fp-nav-color;
                span{
                    background: $fp-nav-color;
                }
            }
        }
        span{
            background: rgba($fp-nav-color, .4);
            &:hover{
                background: $fp-nav-color;
            }
        }
    }
}
