.main-title {
    font-size: rem(52);
    color: $color-text;
    margin-bottom: 0;
    margin: 0;
    font-family: inherit;
    font-weight: 700;
    
    &--light{
        color: #fff;
        + .main-title__brief{
            color: #fff;
        }
    }
    &:not(&--no-underline){
        &:after{
            content: '';
            width: 40%;
            height: 8px;
            background: $color-primary;
            display: block;
            border-radius: 1em;
            margin-top: 15px;
        }
    }
    > p{
        font-size: .4em;
        margin-bottom: 0;
    }
}

.main-title--icon{
    display: flex;
    align-items: center;
    > svg{
        width: 90px;
        height: 90px;
        fill: $color-primary;
        @supports (margin-inline-end: 20px){
            margin-inline-end: 20px;
        }
        @supports not (margin-inline-end: 20px){
            margin-#{$right}: 20px;
        }
    }
}
.main-title__brief{
    margin-top: 4vh;
    margin-bottom: 0;
}